<template>
  <div class="contents-container" style="margin-left: 80%;">
    <router-link v-on:click="this.showRouterViewYn = true;" :to="{ path: '/admin/plantInsert' }">
      <button style="width: 4rem; height: 1.5rem; background-color: lightgreen; border: 1px solid lightgray; border-radius: 10px; 
                    color: #61656A; font-size:0.8rem;">Admin</button>
    </router-link>
  </div>

  <div class="contents_div_1" style="height:95%; overflow:auto;">
    <div style="padding:1rem; margin-right:1rem; margin-bottom:1rem; border=1px solid red;">
      <h2>TO-DO LIST 검색 기능 개발 중</h2>
      1. 입력(날짜/제목/입력)<br />
      2. 목록(번호/제목/날짜)<br />
      3. 페이징 <br />
      4. 글자 수 조정하기(textarea) : https://ui.toast.com/posts/ko_20220318
      <h5>
        listMode : {{this.listMode}} / viewMode : {{this.viewMode}} / writeMode : {{this.writeMode}} / editMode : {{this.editMode}}</h5>
      <div class="container-column" v-if="listMode">
        <input type="button" class="button-board" value="새글쓰기" v-on:click="this.writeMode=true; this.title=null; this.contents=null;" v-if="!this.writeMode" />
        <div class="keyword_list">
          <button class="button_keyword_item" 
                  v-for="item in this.tagList" 
                  v-bind:key="item.id" 
                  v-on:click="this.contentsList(item.id, item.tagValue);">
            #{{ item.tagName }}
          </button>
        </div>
      </div>
      <!-- 조회form -->
      <!-- <div class="container-column" v-if="viewMode">
        <input type="button" class="button-board" value="목록으로" v-on:click="this.viewMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" />
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" />
      <input type="button" class="button-board" value="수정(권한에 따라서 버튼 제거 가능)" v-on:click="doDml('');" />
      </div> -->

      <!-- 새글form -->
      <div class="container-column" v-if="writeMode">
        <input type="button" class="button-board" value="목록으로" v-on:click="this.writeMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" placeholder="제목을 입력해주세요." />
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" style="resize: none" />
        <input type="button" class="button-board" value="등록하기" v-on:click="doDml('insert');" />
      </div>

      <!-- 수정form -->
      <div class="container-column" v-if="editMode">
      <input type="button" class="button-board" value="목록으로" v-on:click="this.editMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" />
        <div>{{ regDt }}</div>
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" />
        <input type="button" class="button-board" value="수정" v-on:click="doDml('update');" />
        <input type="button" class="button-board" value="삭제" v-on:click="doDml('delete');" />
      </div>

      <div class="row">
        <table>
          <tr>
            <!-- <th v-for="(a, i) in resultKey" :key="i">
              <div v-if="this.resultKey[i] !== 'cntnts'">
                {{ this.resultKey[i] }}
              </div>
            </th> -->
            <th>No.</th>
            <th>제목</th>
            <th>작성자</th>
            <th>날짜</th>
            <th>조회</th>
          </tr>
          <tr class="tr-content" style="width:100%;" v-for="(row, rid) in resultArr" :key="rid">
            <td style="width:7%; text-align: center;">
              {{ row.brd_id }}
            </td>
            <td class="td-content" v-on:click="viewData(row.brd_id)">
              {{ row.title }}
            </td>
            <td style="width:12%; text-align: center;">
              관리자
            </td>
            <td style="width:10%; text-align: center;">
              {{ row.reg_dt }}
            </td>
            <td style="width:7%; text-align: center;">
              0
            </td>
          </tr>
          <!-- <tbody>
            <tr v-for="(row, rid) in resultArr" :key="rid" v-on:click="viewData(row.brd_id)" style="cursor: pointer"> -->
              <!-- <td v-for="(col, cid) in row" :key="cid" v-text="col"></td> -->
              <!-- <td v-for="(col, cid) in row" :key="cid">
                <div v-if="cid !== 'cntnts'">{{ col }}</div>
              </td>
            </tr>
          </tbody> -->
        </table>
      </div>
      <div class="row">...페이지...</div>
    </div>
  </div>












  <div class="contents-container">
    <div class="contents-container">
      <div class="contents_title">
        <h3>힐링 활동을 소개합니다.</h3>
      </div>  
    </div>
  </div>
  <!-- <div class="contents-container">
      <div class="contents_item_medium_left">
        contents_item_medium_left
      </div>
      <div class="contents_item_medium_right">
        contents_item_medium_right
      </div>
  </div> -->
  <div class="contents-container">
    <div class="contents-container">
      <div class="contents_item_medium_left">
        <div class="contents-container">
          <div class="contents-container" v-if="showContentYn">
            <div class="contents_item_full" style="border-top: 3px solid #9abf7f; border-bottom: 1px solid #9abf7f;">
              <div style="float:left;">{{ healingActivity.title }}</div>
              <div>{{ healingActivity.reg_dt }}</div>
            </div>
            <div class="contents_item_full" style="border: 1px solid #9abf7f;">
            <!-- <div class="contents_item_full" v-html="healingActivity.cntnts.replace(/(?:\r\n|\r|\n)/g, '<br />')" style="border: 1px solid #9abf7f;"> -->
              <div class="board-contents" v-html="healingActivity.cntnts.split('\n').join('<br />')" >
              </div>      
              <!-- <div class="board-contents">
                {{ healingActivity.cntnts }}
              </div>       -->
            </div>
          </div>
          <div class="contents-container" v-if="showContentYn" style="display: flex; justify-content: center; align-items: center;">
            <div class="contents_item_full">
              <button v-on:click="hideContent();">목록</button>
            </div>
          </div>
          <div class="contents-container">
            <div class="contents_item_full">
              <table>
                <tr>
                  <th>No.</th>
                  <th>제목</th>
                  <th>작성자</th>
                  <th>날짜</th>
                  <th>조회</th>
                </tr>
                <!-- <tr style="width:100%;" v-for="(healingActivity,id) in dataHealingActivity" :key="id" > -->
                <tr style="width:100%;" v-for="healingActivity in displayedPosts" :key="healingActivity.id">
                  <td style="width:7%; text-align: center;">
                    {{ healingActivity.brd_id }}
                  </td>
                  <td class="td-content" v-on:click="showContent(healingActivity);">
                    {{ healingActivity.title }}
                  </td>
                  <td style="width:12%; text-align: center;">
                    관리자
                  </td>
                  <td style="width:10%; text-align: center;">
                    {{ healingActivity.reg_dt }}
                  </td>
                  <td style="width:7%; text-align: center;">
                    0
                  </td>
                </tr>
              </table>
            </div>
            <div class="contents_item_full" style="display: flex; justify-content : center; width:15rem;">
              <!-- <div style="border:1px solid black; display: inline-block;">가운데정렬하시라고</div> -->
              <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
                <button v-if="currentPage > 1" v-on:click="currentPage--">이전페이지</button>
              </div>
              <div style="width:20%; display: flex; justify-content: center; align-items: center; margin:auto;">
                <span>{{ currentPage }} / {{ totalPages }}</span>
              </div>
              <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
                <button v-if="currentPage < totalPages" v-on:click="currentPage++">다음페이지</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="contents_item_medium_right">
        contents_item_medium_right
      </div>
    </div>
  </div>
</template>
  
<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);
// const dataHealingActivityJson = healingActivityJson;
const healingActivity = {};
const showContentYn = false;

  export default {
    props: [], 
    components: {
    },
    data() {
      return {
        inputTag: "",
        tagId: "0",
        tagList: [
          { id: 0, tagName: "전체", tagValue: undefined},
          { id: 1, tagName: "친환경", tagValue: "친환경"},
          { id: 2, tagName: "재활용재사용", tagValue: "재활용재사용"},
          { id: 3, tagName: "비용절감", tagValue: "비용절감"},
          { id: 4, tagName: "반려식물", tagValue: "반려식물"},
          { id: 5, tagName: "흙", tagValue: "흙"},
          { id: 6, tagName: "빛", tagValue: "빛"},
          { id: 7, tagName: "온", tagValue: "온"},
        ],
        listMode: true,
        viewMode: false,
        writeMode: false,
        editMode: false,
        title: "",
        contents: "",
        regDt: "", 
        resultArr: [],
        // resultKey: [],
        img: "@/assets/images/plant1_01.jpg",





        dataHealingActivity: healingActivityJson,
        healingActivity,
        showContentYn,
        currentPage: 1,
        postsPerPage: 20,
      };
    },
    computed: {
      displayedPosts() {
        const startIndex = (this.currentPage - 1) * this.postsPerPage;
        const endIndex = startIndex + this.postsPerPage;
        return this.dataHealingActivity.slice(startIndex, endIndex);
      },
      totalPages() {
        return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
      }
    },
    created() {
    },
    mounted() {
      this.contentsList();
    },
    update() {
    },
    methods: {
      contentsList(id, tagName) {
      var contentsId = null;
      if (id == 0 || id == undefined) {
        contentsId = 9;   // 전체 게시글 가져오기
      } else {
        contentsId = 13;  // 선택한 키워드 관련글들 가져오기
      }
      // console.log(contentsId, ' ', tagName);

      this.listMode = true;
      this.viewMode = false;
      this.writeMode = false;
      this.editMode = false;
      
      axios
        // .post("/api/boardMas", { dml: '', contentsId: "9" })
        .post("/api/board", { dml: '', contentsId: contentsId, tag: tagName })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          // this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
          // this.insert = false;
          this.update = false;
        })
        .catch((error) => {
          console.log(error);
        });

      



      // get from json
      axios
        .get("/api/json/test", {
          responseType: 'json', // JSON 형식으로 응답 데이터 받기
        })
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          console.log('get from json : this.resultArr : ', this.resultArr);
        })
        .catch((error) => {
          console.log(error);
        });




      
      this.brdId = null;
      // this.insert = false;
      this.update = false;
    },
    viewData(brdId) {
      console.log('row : ', brdId);
      this.listMode = false;
      this.viewMode = false;
      this.writeMode = false;
      this.editMode = true;

      var resultJson;
      var resultArr;
      // var resultKey;

      const axios = require("axios");
      axios
        // .post("/api/board", { dml: '', brdId: brdId })
        .post("/api/board", { dml: '', contentsId: 9, brdId: brdId })
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          // this.resultKey = Object.keys(resultArr[0]);
          
          this.title = resultArr[0].title;
          this.contents = resultArr[0].cntnts;
          this.regDt = resultArr[0].reg_dt;
          console.log('this.regDt :: ', this.regDt);
          
          this.brdId = brdId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });
        this.listMode=false;
        // if(수정권한있으면(작성자,관리자)) 
        //   this.editMode=true;
        // else 
        //   this.viewMode=true;
        this.editMode=true;
    },
    doDml(dml) {
      if ( this.title.length > 0 && this.contents.length > 0) {
        
        var resultMsg;
        const axios = require("axios");
        axios
          .post("/api/board", { dml: dml, brdId: this.brdId, title: this.title, contents: this.contents})
          .then((response) => {
            // console.log('response.data : ',response.data.result);
            resultMsg = response.data.result;
            console.log('resultMsg : ',resultMsg);
              
            // alert(resultMsg);
            this.editMode = false;
            this.listMode = true;
            this.contentsList();
          })
          .catch((error) => {
            console.log(error);
          });   
      } else {
        alert("제목과 내용 입력은 필수입니다.");
      }
    },






      showContent(healingActivity) {
        this.healingActivity = healingActivity;
        this.showContentYn = true;
      },
      hideContent() {
        this.showContentYn = false;
      }
    },
  };
  </script>